.banner {
  padding: 0;
  margin-bottom: 2rem;
  border: 0;
}

.banner__container {
  display: block;
  padding: 2.2rem 0;
  background-color: #f3f4f0;
  color: #000000;
  transition: all 0.3s ease-in-out;
  background-size: cover;
  background-position: 0 50%;
  font-weight: 700;
  min-height: 400px;
}

.banner__container:focus,
.banner__container:hover {
  text-decoration: none;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
  color: #000000;
}

.banner__container:focus .banner__text-preview,
.banner__container:hover .banner__text-preview {
  text-decoration: underline;
}

.banner__container:active {
  opacity: 0.5;
}

.banner__name-page {
  text-transform: uppercase;
  font-size: 1.25rem;
  margin-bottom: 1rem;
  letter-spacing: 0.04em;
  font-size: 24px;
  font-weight: 700;
  border: none;
}

.banner__name-page:focus,
.banner__name-page:hover,
.banner__name-page:active {
  text-decoration: none;
}

.banner__title {
  color: #ffffff;
  background-color: #3490dc;
  font-size: 22px;
  padding: 0.5rem 0;
  padding-left: 4%;
  padding-right: 1rem;
  margin-bottom: 3.6rem;
  display: inline-flex;
  min-width: 50%;
}

.banner__text-preview {
  font-size: 1.2rem;
  margin-bottom: 0;
  color: #000000;
}

@media (min-width: 720px) {
  .banner__title {
    padding-left: 7%;
  }
}

@media (min-width: 992px) {
  .banner__container {
    padding: 2.5rem 0;
    background-size: cover;
    background-position: 50% 100%;
    background-repeat: no-repeat;
  }

  .banner__container .main-container {
    width: 100%;
  }

  .banner__container .banner__text-preview {
    margin: 0;
    font-size: 1.2rem;
    padding-left: 2rem;
    width: 70%;
    max-width: 870px;
  }

  .banner__name-page {
    font-size: 0.71rem;
    letter-spacing: 0.02em;
    width: 100%;
  }

  .banner__title {
    font-size: 0.71rem;
    margin-bottom: 2rem;
    padding-left: 2rem;
    width: 70%;
    max-width: 870px;
  }
}

.quote {
  display: flex;
}

.quote p {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 700;
  margin-bottom: 20px;
}

.quote a {
  color: #000000;
  text-decoration: underline;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 400;
}

.quote__text {
  background-color: #f3f4f0;
  background-image: url("../images/bg-quote.png");
  background-size: cover;
  background-position: left center;
  background-repeat: no-repeat;
}

.quote__img {
  overflow: hidden;
}

.quote__img img {
  object-fit: cover;
  width: auto;
  height: 100%;
}

